import { Injectable } from '@angular/core';
import {Observable} from "rxjs";
import {HttpClient} from "@angular/common/http";
import {environment} from "../../environments/environment";

@Injectable
({
  providedIn: 'root'
})
export class AuthenticationService
{
  private isAuthenticated = false;

  private BACKEND_URL = environment.BACKEND_URL;

  constructor(private http: HttpClient)
  {

  }

  public getIsAuthenticated()
  {
    return this.isAuthenticated;
  }

  createUser(username: string, password: string, role: string, email: string, phone: string): Observable<any>
  {
    const userData = {username, password, role, email, phone};

    return this.http.post
    (
      this.BACKEND_URL+ '/user/signup',
      userData
    );
  }
}
